.coin-container {
  display: flex;
  justify-content: center;
}
.coin-row {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  border-bottom: 3px solid grey;
  width: 900px;
  height: 80px;
}
.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}
.coin img {
  height: 30px;
  margin-right: 10px;
}
.coin h1 {
  font-size: 24px;
  width: 140px;
}
.coin p {
  font-size: 17px;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: right;
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 110px;
}

.coin-volume {
  width: 155px;
}

.coin-marketcap {
  width: 230px;
}

.coin-percent {
  width: 100px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}
